<template>
  <div id="TypeQuickly" class="pb-16">
    
    <Alert ref="Alert"/>

    <InteractiveBord @dataForApp="updateTimeOver" @changeSetting="changeSettings()" :maxW="'max-w-3xl'" :timeLimit="level == 0 ? 8:100" :score="level == 0 ? score :(cLC - wLC) < 0 ? 0 : cLC - wLC" ref="InteractiveBord"/>

    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl md:max-w-3xl mt-3">

        <div class="w-full flex justify-center px-3">
          <div class="relative w-4/5 flex rounded-lg overflow-hidden">
            <span class="w-full pt-3 bg-red-500"></span>
            <span :style="'width:'+(cLC/(targetText.length - (targetText.split(' ').length-1)))*100+'%'" class="absolute transition pt-3 bg-green-500"></span>
          </div>
        </div>

        <div class="w-full flex justify-center">
          <div class="p-3 w-full h-64">
            <div class="relative h-full w-full p-5 md:p-10 flex flex-wrap items-center justify-center rounded-lg overflow-y-auto border text-2xl font-bold bg-purple-800" >
              <span class="flex w-auto h-auto" v-for="(ward, i) in targetTextWithColors" :key="i">
                  <span v-for="(letter, n) in ward" :key="n" :style="'color :'+letter.color" v-text="letter.letter" />
                  <span class="px-1" />
              </span>
              <div class="absolute inset-0 opacity-0"/>
            </div>
          </div>
        </div>

        <div class="w-full flex justify-center">
          <div class="p-3 w-full h-20">
            <input type="text" :disabled="inputDisabled" :maxlength="targetText.length+200" v-model="userText" class="h-full w-full px-5 flex items-center justify-center rounded-lg overflow-hidden border text-2xl font-bold bg-purple-800 focus:outline-none" placeholder="write text here">
          </div>
        </div>

        <div class="w-full flex justify-center items-center mt-10 font-bold text-md lg:text-xl">
          <button class='bg-red-500 rounded-lg py-2 px-4 leading-tight focus:outline-none cursor-pointer' @click="stop()">Stop Game</button>
        </div>

      </div>
    </div>

    <LevelsSelect @dataForApp="updateLevelValue" :title="'Type'" :levels="['Words','Paragraphs']"/>

    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl md:max-w-3xl mt-3">
        <div class="w-full mt-10 flex justify-center">
          <div class="px-5 w-full flex flex-wrap text-2xl">
            <div class="w-full p-5 flex flex-wrap rounded-lg overflow-hidden border text-2xl font-bold bg-purple-800">
              <span class="w-1/2 text-green-300">All : <span class="text-green-500" v-text="aLC" /></span>
              <span class="w-1/2 text-green-300">Correct : <span class="text-green-500" v-text="cLC" /></span>
              <span class="w-1/2 text-red-300">Wrong : <span class="text-red-500" v-text="wLC" /></span>
              <span class="w-1/2 text-blue-300">Skipping : <span class="text-blue-500" v-text="sLC" /></span>
              <span class="w-full mt-5 text-blue-300">Your Typing Speed is : <span class="text-blue-500" v-text="typeSpeed(aLC) +' l/s'" /></span>
              <span class="w-full text-green-300">Your Correct Typing Speed is : <span class="text-green-500" v-text="typeSpeed(cLC) +' l/s'" /></span>
            </div>
          </div>
        </div>
      </div>
    </div>
    

  </div>
</template>

<script>
import Alert from '../components/Alert'
import InteractiveBord from '../components/InteractiveBord'
import LevelsSelect from '../components/LevelsSelect'
export default {
  name: 'TypeQuickly',
  components: {
    Alert,
    InteractiveBord,
    LevelsSelect
  },
  data() {
    return {
      targetText:"",
      userText:"",
      inputDisabled:false,

      userTextShift:0,

      score:0,

      aLC:0, //allLetterCount
      cLC:0, //correctLetterCount
      wLC:0, //wrongLetterCount
      sLC:0, //skinpLetterCount

      level:0,

      targetTextWithColors:[],
      colors:{"Default":"black","Right":"green","Wrong":"red","Skipping":"blue"},

      texts_words:["games", "fast","type","word","web","algoarthim","learning","keyboard","laptop","mobile"],
      texts_paragraphs:[
        "musab mohammed abou algasim hassan",
        "True friendship is perhaps the only relation that survives the trials and tribulations of time and remains unconditional. A unique blend of affection, loyalty, love, respect, trust and loads of fun is perhaps what describes the true meaning of friendship. Similar interests, mutual respect and strong attachment with each other are what friends share between each other. These are just the general traits of a friendship. To experience what is friendship, one must have true friends, who are indeed rare treasure.",
        "A computer programmer, sometimes called only programmer or more recently a coder (especially in more informal contexts), is a person who creates computer software. The term computer programmer can refer to a specialist in one area of computers, or to a generalist who writes code for many kinds of software. A programmer's most oft-used computer language (e.g., Assembly, COBOL, C, C++, C#, JavaScript, Lisp, Python) may be prefixed to the term programmer. Some who work with web programming languages also prefix their titles with web.",
        "Sudan, is a country in Northeast Africa. Bordered by Egypt to the north, Libya to the northwest, Chad to the west, the Central African Republic to the southwest, South Sudan to the south, Ethiopia to the southeast, Eritrea to the east, and the Red Sea to the northeast. Sudan has a population of 43 million and occupies 1,886,068 square kilometres (728,215 square miles), making it Africa's third-largest country and also the third-largest in the Arab world. It was the largest country in Africa and the Arab world by area before the 2011 South Sudan's",
        "Real Madrid, meaning Royal Madrid Football Club), commonly referred to as Real Madrid, is a Spanish professional football club based in Madrid. Founded on 6 March 1902 as Madrid Football Club, the club has traditionally worn a white home kit since inception. The word real is Spanish for royal and was bestowed to the club by King Alfonso XIII in 1920 together with the royal crown in the emblem. The team has played its home matches in the 81, 044-capacity Santiago Bernabéu Stadium in downtown Madrid since 1947. Unlike most European sporting entities, Real Madrid's members (socios) have owned and operated the club throughout its history.",
        "The user interface (UI), in the industrial design field of human-computer interaction, is the space where interactions between humans and machines occur. The goal of this interaction is to allow effective operation and control of the machine from the human end, whilst the machine simultaneously feeds back information that aids the operators' decision-making process. Examples of this broad concept of user interfaces include the interactive aspects of computer operating systems, hand tools, heavy machinery operator controls, and process controls. The design considerations applicable when creating user interfaces are related to or involve such disciplines as ergonomics and psychology.",
        "A father is the male parent of a child. Besides the paternal bonds of a father to his children, the father may have a parental, legal, and social relationship with the child that carries with it certain rights and obligations. An adoptive father is a male who has become the child's parent through the legal process of adoption. A biological father is the male genetic contributor to the creation of the infant, through sexual intercourse or sperm donation  A biological father may have legal obligations to a child not raised by him, such as an obligation of monetary support"
      ]
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      if(this.level == 0){
        this.targetText = this.texts_words[Math.floor(Math.random() * this.texts_words.length)]
      }else{
        this.targetText = this.texts_paragraphs[Math.floor(Math.random() * this.texts_paragraphs.length)]
      }
      this.addColorText();
    },
    addColorText(){
      var rrr = [];
      for (let i = 0; i < this.targetText.split(" ").length; i++) {
        rrr.push([]);
        var ward = this.targetText.split(" ")[i];
        for (let n = 0; n < ward.length; n++) {
          rrr[i].push({
            letter:ward[n],
            color:this.colors.Default
          })
        }
      }

      
      this.targetTextWithColors = rrr
    },
    userTextChange(){
      var vm=this;
      if(!vm.$refs.InteractiveBord.timerInterval && vm.userText.length != 0) vm.$refs.InteractiveBord.startTimer();


      vm.targetTextWithColors.forEach(ward => {
        ward.forEach(letter => {
          letter.color = this.colors.Default
        });
      });
      vm.check();
    },
    check(){
      var vm=this;
      vm.aLC = 0;vm.cLC = 0;vm.wLC = 0;vm.sLC = 0;

      for (let i = 0; i < vm.userText.split(" ").length; i++) {
        if(i >= vm.targetTextWithColors.length)continue
        let shift = 0;
        for (let n = 0; n < vm.userText.split(" ")[i].length; n++) {
          if(n + shift >= vm.targetTextWithColors[i].length)continue

          vm.aLC += 1;
          let TTWC = vm.targetTextWithColors[i];

          if(TTWC[n + 1 + shift] && vm.userText.split(" ")[i][n] != TTWC[n + shift].letter){
            if(vm.userText.split(" ")[i][n] == TTWC[n + 1 + shift].letter && TTWC[n + 1 + shift].letter != " "){
              TTWC[n + shift].color = vm.colors.Skipping
              vm.sLC += 1;
              shift += 1;
            }
          }

          if(vm.userText.split(" ")[i][n] == TTWC[n + shift].letter){
            TTWC[n + shift].color = vm.colors.Right
            vm.cLC += 1;
          }else{
            TTWC[n + shift].color = vm.colors.Wrong
            vm.wLC += 1;
          }
        }
      }
      this.ifAllDone();
    },
    ifAllDone(){
      if(this.cLC == (this.targetText.length - (this.targetText.split(' ').length-1))){
        if(this.level == 0){
          
          this.$refs.InteractiveBord.stopTimer(1);
          this.$refs.InteractiveBord.startTimer();
          this.score++;
          this.userText = "";
          (this.$refs.Alert).alertShow("Done","bg-green-500",500);
          this.MStart()

        }else{

          this.$refs.InteractiveBord.stopTimer();
          (this.$refs.Alert).alertShow("Done","bg-green-500",10000);
          console.log("all Done")
          this.inputDisabled = true;

        }
      }
    },
    stop(){
      this.$refs.InteractiveBord.stopTimer();
      (this.$refs.Alert).alertShow("Stop","bg-red-500",10000);
      console.log("Stop")
      this.inputDisabled = true;
    },

    typeSpeed(num){
      if(!this.$refs.InteractiveBord)return 0;
      if(!this.$refs.InteractiveBord.timePassed || this.$refs.InteractiveBord.timePassed == 0)return 0;

      return (num / this.$refs.InteractiveBord.timePassed).toFixed(3)
    },

    updateTimeOver(){
      this.inputDisabled = true;
      this.$refs.Alert.alertTimeOver();
    },
    updateLevelValue(value){
      this.level = value;
      this.changeSettings();
    },

    changeSettings(){
      var vm = this;
      var level = vm.level;
      vm.$refs.InteractiveBord.stopTimer(1);
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.MStart();
    }
  },
  watch: {
    userText(){
      this.userTextChange()
    }
  },
}
</script>

